function dataURLtoFile(dataurl) {
    var arr = dataurl.split(','),
      mime = arr[0].match(/:(.*?);/)[1],
      bstr = atob(arr[1]),
      n = bstr.length,
      u8arr = new Uint8Array(n);
    while (n--) {
      u8arr[n] = bstr.charCodeAt(n);
    }
    return new File([u8arr], { type: mime });
  }
  //
  export function compressImg(file){
    var files;
    var fileSize = parseFloat(parseInt(file['size'])/1024).toFixed(2);
    var read = new FileReader();
    read.readAsDataURL(file);
    return new Promise(function(resolve){
     read.onload = function (e) {
       var img = new Image();
       img.src = e.target.result;
       img.onload = function(){
         //
         var w = this.width,
           h = this.height
         //canvas
         var canvas = document.createElement('canvas')
         var ctx = canvas.getContext('2d')
         var base64;
         // 
         canvas.setAttribute("width", w)
         canvas.setAttribute("height", h)
         ctx.drawImage(this, 0, 0, w, h)
         if(fileSize<300){
           // 300KB 
           base64 = canvas.toDataURL(file['type'], 1)
         } else {
            let fl = parseFloat((200 /fileSize).toFixed(1))
            base64 = canvas.toDataURL(file['type'], fl > 0.4? 0.4: fl)
         }
         console.log(base64)
         // filebase64file
         files = dataURLtoFile(base64) //base64
         
         resolve(files)
       }
     }
    })
  }
